axios.interceptors.response.use((res) => {
  if (res.status >= 200 && res.status <= 300) {
    return res;
  }
  //由后端抛出的错误
  return Promise.reject(res);
}, (error) => {
  //由网络服务器抛出的错误
  return Promise.reject(error);
});

let catchErrorEvt = function (error) {
  console.log(error.status);
  switch (error.status) {
    case 404:
      console.log('接口不存在');
      break;
    case '500':
      console.log('接口报错');
      break;
    default:
      console.log('其他类型错误');
  }
};

let nullToString = function (data) {
  for (let it in data) {
    if (typeof(data[it]) === "object") {
      nullToString(data[it]);
    }
    data[it] = data[it] == null ? '' : data[it];
  }
  return data;
};

let encrypt = function (data) {
  return encodeURIComponent(data);
};

let decrypt = function (data) {
  return data;
};

export default {
  url,
  post: function (target, params = {}) {
    return new Promise((resolve, reject) => {
      console.time(target);
      axios({
        url: target,
        method: 'post',
        headers: {
          'X-Requested-With': 'XMLHttpRequest',
          'Content-Type': 'application/x-www-form-urlencoded'
        },
        data: params,
        transformRequest: [function (data) {
          let ret = '';
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }]
      }).then(res => {
        resolve(nullToString(res.data));
        console.timeEnd(target);
      }).catch(error => {
        catchErrorEvt(error);
        reject(error);
      });
    });
  },
  get: function (target) {
    return new Promise((resolve, reject) => {
      console.time(target);
      axios({
        url: target,
        method: 'get',
        headers: {
          'X-Requested-With': 'XMLHttpRequest',
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then(res => {
        resolve(nullToString(res.data));
        console.timeEnd(target);
      }).catch(error => {
        catchErrorEvt(error);
        reject(error);
      });
    });
  }
}



